import React from 'react';
import './Promotion.css';
import { Link } from 'react-router-dom';
import newPhones from '../../db/newPhones';

function Promotion() {
  return (
    <div className='new_phones'>
      <h3 className='phones_title'>Premijere</h3>
      {newPhones.map((phone, index) => {
        const { brand, model, img } = phone;
        return (
          <Link key={index} to='/' className='phone'>
            <img src={img} alt={model} className='phone_img' />
            <h4 className='phone_title'>{brand}</h4>
            <p className='phone_model'>{model}</p>
          </Link>
        );
      })}
    </div>
  );
}

export default Promotion;
