import React from 'react';
import './Home.css';
import phoneData from '../db/phoneData';
import phonePrice from '../db/priceSearch';
import { Link } from 'react-router-dom';
import Articles from '../components/Articles/Articles';
import LatestNews from '../components/LatestNews/LatestNews';
import Promotion from '../components/Promotion/Promotion';
import Button from '../components/Buttons/Button';

function Home() {
  return (
    <div className='main'>
      <div className='main_phone'>
        {phoneData.map((phone) => {
          const { id, brand, title, img } = phone;
          return (
            <Link className='main_phoneContainer' to='/' key={id}>
              <img src={img} alt={title} className='main_phonePicture' />
              <div className='main_phoneDesc'>
                <h5 className='main_phoneTitle'>{brand}</h5>
                <p className='main_phoneModel'>{title}</p>
              </div>
            </Link>
          );
        })}
      </div>
      <div className='price'>
        {phonePrice.map((itemPrice, index) => {
          const { name, color } = itemPrice;
          return (
            <Link
              to='/'
              key={index}
              className='filter_price'
              style={{ backgroundColor: `${color}` }}
            >
              {name}
            </Link>
          );
        })}
      </div>
      <Button />
      <div className='flex'>
        <Articles />
        <LatestNews />
        <Promotion />
      </div>
    </div>
  );
}

export default Home;
