import './backToTop.css';

const BackToTop = () => {
  const handleClick = () => {
    console.log('click');
    window.scrollTo({
      top: 0,
      behavior: 'smooth',
    });
  };

  return (
    <div className='footer-btn'>
      <button className='footer-backBtn' onClick={handleClick}>
        Back to top
      </button>
    </div>
  );
};

export default BackToTop;
