import React from 'react';
import './CommercialLeft.css';
const url =
  'https://rsgde.adocean.pl/files/akaqpbtdkpe/vhiehjdkkl/vgpjfkpnjj/300x600.html';

function CommercialLeft() {
  return (
    <aside className='aside'>
      <iframe
        title='reklama'
        className='aside_commercial'
        src={url}
        frameBorder='0'
      ></iframe>
    </aside>
  );
}

export default CommercialLeft;
