import React from 'react';
import { Link } from 'react-router-dom';
import { FaSearch } from 'react-icons/fa';
import './Header.css';

function Header() {
  return (
    <header className='header'>
      <h4 className='logo'>
        <Link to='/' className='logo_img'>
          Mobilni<span>svet</span>.rs
        </Link>
      </h4>
      <div className='search'>
        <FaSearch />
        <input type='text' />
      </div>
      <nav className='navigation'>
        <Link to='/mobilni'>Mobilni</Link>
        <Link to='/cenovnici'>Cenovnici</Link>
        <Link to='/arena'>Arena</Link>
        <Link to='/oglasi'>Oglasi</Link>
        <Link to='/forum' className='link'>
          Forum
        </Link>
      </nav>
    </header>
  );
}

export default Header;
