import './App.css';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';
import Header from './components/Header/Header';
import Footer from './components/Footer/Footer';
import Commercial from './components/Commercial/CommercialTop';
import CommercialLeft from './components/Commercial/CommercialLeft';
import Mobilni from './pages/Mobilni';
import Cenovnici from './pages/Cenovnici';
import Arena from './pages/Arena';
import Oglasi from './pages/Oglasi';
import Forum from './pages/Forum';
import Home from './pages/Home';

import CommercialLeftDown from './components/Commercial/CommercialLeftDown';

function App() {
  return (
    <Router>
      <div className='container'>
        <Commercial />
        <CommercialLeft />
        <CommercialLeftDown />
        <div className='content'>
          <Header />
          <Switch>
            <Route exact path='/'>
              <Home />
            </Route>
            <Route path='/mobilni'>
              <Mobilni></Mobilni>
            </Route>
            <Route path='/cenovnici'>
              <Cenovnici></Cenovnici>
            </Route>
            <Route path='/arena'>
              <Arena></Arena>
            </Route>
            <Route path='/oglasi'>
              <Oglasi></Oglasi>
            </Route>
            <Route path='/forum'>
              <Forum></Forum>
            </Route>
          </Switch>
          <Footer />
        </div>
      </div>
    </Router>
  );
}

export default App;
