import React from 'react';
import buttons from '../../db/buttons';
import { Link } from 'react-router-dom';
import './Button.css';

function Button() {
  return (
    <div className='button_container'>
      {buttons.map((button, index) => {
        const { icon, title } = button;
        return (
          <Link to='/' key={index} className='button'>
            <i className={`button_icon ${icon}`}></i>
            <h3 className='button_title'>{title}</h3>
          </Link>
        );
      })}
    </div>
  );
}

export default Button;
