import React from 'react';
import { Link } from 'react-router-dom';
import tests from '../../db/articles';
import './Articles.css';

function Articles() {
  return (
    <>
      {' '}
      <div className='article_container'>
        {tests.map((test, index) => {
          const { title, text, image } = test;
          return (
            <div className='article_test' key={index}>
              <Link className='article_title' to='/'>
                {title}
              </Link>

              <img src={image} alt='image1' className='article_image' />
              <p className='article_text'>{text}</p>
            </div>
          );
        })}
      </div>
    </>
  );
}

export default Articles;
